import React, { Component } from "react";
import FormRekening from "./Component/FormRekening";
import Table from "./Component/Table";
import { connect } from "react-redux";
import notrespond from "../Images/500.svg";
import { editRekening, getrekeningList } from "../actions/rekeningAction";
import Swal from "sweetalert2";
import FormRekeningEdit from "./Component/FormRekeningEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      let data = {
        data: {
          noRekening: kode,
        },
      };
      AxiosMasterDelete("rekening/delete/" + kode)
        .then(() => dispatch(hideModal()))
        .then((res) =>
          berhasil("Data Berhasil Dihapus!")
            .then(() => LogData("Data Berhasil Dihapus Dari Rekening", data))
            .then(() => dispatch(getrekeningList()))
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}

const defaultSorted = [
  {
    dataField: "nomorRekening",
    order: "asc",
  },
];
const maptoState = (state) => {
  return {
    daftarList: state.provinsi.rekening,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class DaftarRekening extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Rekening: [],
      response: "",
      columns: [
        {
          dataField: "no_rekening",
          text: "Nomor Rekening",
        },
        {
          dataField: "nama_bank",
          text: "Nama Bank",
        },
        {
          dataField: "atas_nama",
          text: "Atas Nama",
        },

        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_3"
                  onClick={() => hapus(this.props.dispatch, row.no_rekening)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning"
                  data-tut="reactour_4"
                  onClick={() =>
                    this.props.dispatch(
                      editRekening(
                        row.no_rekening,
                        row.atas_nama,
                        row.nama_bank,
                        this.edithModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getrekeningList());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getrekeningList());
  };

  handleSubmit(hasil) {
    let data = {
      atas_nama: hasil.atasNama,
      nama_bank: hasil.namaBank,
      no_rekening: hasil.noRekening,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("rekening/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Disimpan!")
          .then(() => LogData("Data Berhasil Disimpan Di Rekening", data))
          .then(() => this.props.dispatch(getrekeningList()))
          .then(() => this.props.dispatch(reset("DataRekening")))
      )
      .catch((err) =>
        this.handleReactive(err, hasil.noRekening, {
          atas_nama: hasil.atasNama,
          nama_bank: hasil.namaBank,
        })
      );
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(
          err,
          kode,
          "rekening/reactive/",
          data,
          "rekening/edit/"
        ).then(() => this.props.dispatch(getrekeningList()))
      : gagal("Data Gagal Ditambahkan");
  }
  handleEdit(hasil) {
    let data = {
      atas_nama: hasil.atasNama,
      nama_bank: hasil.namaBank,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("rekening/edit/" + hasil.noRekening, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!")
          .then(() => LogData("Data Berhasil Diubah Di Rekneing", data))
          .then(() => this.props.dispatch(getrekeningList()))
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!").then(() =>
          LogData("Data Gagal Diubah di rekening", data, err)
        )
      );
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  edithModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Rekening Bank, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data Rekening Bank yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Rekening" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormRekeningEdit
                        updateStatus={this.updateStatus}
                        onSubmit={(data) => this.handleEdit(data)}
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <FormRekening
                        updateStatus={this.updateStatus}
                        onSubmit={(data) => this.handleSubmit(data)}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Rekening"
                      : "Tambah Data Rekening"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Rekening
                    </button>
                  </div>
                </div>
                <div className="col-lg-12" data-tut="reactour_2">
                  {this.props.daftarList ? (
                    <Table
                      data={this.props.daftarList}
                      columns={this.state.columns}
                      defaultSorted={defaultSorted}
                      namaKey="noRekening"
                    />
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="ERROR GET DATA"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(DaftarRekening);
