import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validaitonRekening from "../../Validations/validaitonRekening";
import { renderField } from "../../renderField";

class FormRekening extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      no_rekening: "",
      nama_bank: "",
      atas_nama: "",
      mata_uang: "RUPIAH",
    };
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4 col-md-4">
              <Field
                name="noRekening"
                type="tel"
                component={renderField}
                label="Nomor Rekening"
                onChange={() => this.props.change("mataUang", "DEFAULT")}
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="namaBank"
                type="telp"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Nama Bank"
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="atasNama"
                type="text"
                normalize={(value) => value && value.toUpperCase()}
                component={renderField}
                label="Atas Nama"
              />
            </div>
            {/* <div className="col-lg-4 col-md-4">
              <label htmlFor="">Mata Uang</label>
              <Field
                name="mataUang"
                component="select"
                className="form-control"
                label="Mata Uang"
                value="DEFAULT"
              >
                <option value="DEFAULT">SILAHKAN PILIH</option>
                <option value="RUPIAH">RUPIAH</option>
                <option value="USD">USD</option>
              </Field>
            </div> */}
            <div className="col-md-12 col-lg-12 mt-5">
              <div className="text-right">
                <button
                  type="submit"
                  className="btn btn-primary"
                  disabled={this.props.onSend}
                >
                  {this.props.onSend ? (
                    <span
                      className="spinner-border spinner-border-sm mr-3"
                      aria-hidden="true"
                    ></span>
                  ) : null}
                  Simpan
                  <i className="fa fa-paper-plane ml-3"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormRekening = reduxForm({
  form: "DataRekening",
  validate: validaitonRekening,
  enableReinitialize: true,
})(FormRekening);
export default connect()(FormRekening);
